import { useState, useEffect, Suspense } from 'react';
import { Outlet, Link, useParams, useLocation } from 'react-router-dom';
import {LoaderSpinner} from '../../components/Loader/Loader';
import {MovieDetails} from '../../components/MovieDetails/MovieDetails';
import { fetchMoviesId} from '../../API/MoviesApi';
import styles from '../Page/MoviePage.module.css';
import { ButtonLinkStyled } from './Btn.styled';
import React from 'react';
import { IData } from '../../interfaces';

 const  MoviePage = (): JSX.Element =>{
  const location = useLocation();
  const [movie, setMovie] = useState<Partial<IData>>();
  const { movieId } = useParams();
  // const { url } = useMatch();
  useEffect(() => {
fetchMoviesId(movieId).then(({ data }) => setMovie(data));
  }, [movieId]);

  // const goBack = () => {
  //   navigate({
  //     pathname: location.state.from.search || '/movies',
  //     search: location.state?.searchValue || '',
  //   });
  // };

  return (
    <>
      <ButtonLinkStyled
        to={location?.state ?? '/'}
        type="button"
        className={styles.button}
      >
        Go back
      </ButtonLinkStyled>
      {movie && (
        <>
          <div className={styles.wrapper}>
            <MovieDetails movie={movie} />
            <h4 className={styles.addTitle}>Additional information:</h4>
            <Link
              to={{
                pathname: `/movies/${movieId}/cast`,
              }}
              state={{ from: location.state }}
              className={styles.link}
            >
              Cast
            </Link>
            <Link
              to={{
                pathname: `/movies/${movieId}/reviews`,
              }}
              state={{ from: location.state }}
              className={styles.link}
            >
              Reviews
            </Link>
            <Suspense fallback={<LoaderSpinner />}>
              <Outlet />
            </Suspense>
          </div>
        </>
      )}
    </>
  );
}

export default MoviePage